/******* Media query Mixing *******/
body {
  font-family: 'Open Sans', sans-serif; }

#title {
  letter-spacing: 1px; }

.chips {
  min-height: 30px;
  border: none;
  border-radius: 3px;
  line-height: 1;
  font-size: 1em;
  position: relative; }

.chips-input {
  display: inline-block;
  min-height: 30px;
  margin-bottom: 5px;
  margin-left: 10px;
  border: 0;
  outline: none;
  font-size: 0.9rem; }

.chip {
  display: inline-block;
  margin-top: 5px;
  margin-bottom: 5px;
  margin-left: 5px;
  margin-right: 25px;
  position: relative; }
  .chip .chip-value {
    display: inline-block;
    padding: 5px 7.5px 5px 15px;
    /*padding-left: $chip-y-spacing;
    padding-right: $chip-y-spacing / 2;*/
    background: #ccc;
    color: rgba(0, 0, 0, 0.6);
    font-weight: normal;
    border-radius: 15px 0 0 15px; }
  .chip .chip-delete-button {
    background: #ccc;
    padding: 0 12px;
    color: rgba(0, 0, 0, 0.6);
    border: 0;
    border-radius: 0 15px 15px 0;
    padding: 5px 10px;
    cursor: pointer;
    position: absolute;
    top: 0;
    bottom: 0;
    right: -25px;
    line-height: 0.5;
    font-weight: bold; }
  .chip.single {
    padding: 11px 10px;
    border-radius: 5px;
    border: none;
    background: #ffffff;
    font-size: 0.875rem;
    color: #333333;
    width: 100%;
    margin: 10px 0; }
    .chip.single .chip-value {
      background: transparent !important;
      padding: 0 !important;
      color: #000000 !important;
      font-size: 0.875rem !important; }
    .chip.single .chip-delete-button {
      right: 5px !important;
      background-color: transparent !important; }

.auto-suggest {
  border: 1px solid #ddd;
  border-top: none;
  position: absolute;
  width: 100%;
  display: none;
  background: #ddd;
  overflow: auto;
  max-height: 330px;
  z-index: 1;
  border-bottom-left-radius: 3px;
  border-bottom-right-radius: 3px; }
  .auto-suggest ul {
    margin: 0 !important;
    list-style: none;
    padding-left: 0; }
  .auto-suggest li {
    background: #ffffff;
    border: 2px solid #ffffff;
    padding: 5px 10px;
    font-size: 14px;
    cursor: pointer;
    display: flex; }
    .auto-suggest li:hover {
      border: 2px dashed #0c7dbf; }
    .auto-suggest li .image-style {
      min-width: 60px;
      max-width: 60px; }
    .auto-suggest li .user-name {
      padding: 10px;
      color: #000000; }
  .auto-suggest.show {
    display: block; }

